.text_line {
    font-size: 18px;
    color: #242424;
    border-bottom: 1px solid #EBEBEB;
    padding-bottom: 10px;
    margin: 0 0 10px 0;

    &.margin {
        margin-left: -20px;
        margin-right: -20px;
        padding-left: 20px;
        padding-right: 20px;
    }

    svg {
        width: 18px;
        height: 18px;
        color: var(--yellow);
        vertical-align: top;
        margin-right: 5px;
    }
}

