// FORM DEFAULT
.title_form {
    text-align: center;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.form_default {
    .icon-visible {
        svg {
            fill: var(--green1);
            position: absolute;
            top: 11px;
            left: 10px;
        }

        .form-control {
            padding-left: 35px;
        }
    }

    .form-control {
        padding: 10px;
    }

    .form-group {
        margin-bottom: 17px;
        display: flex;
        flex-wrap: wrap;

        &.block {
            display: block
        }

        .lbl {
            width: 150px;
            margin-top: 12px;
        }

        .wrap-input {
            width: calc(100% - 150px);
        }

        &.male {
            .lbl {
                margin-top: 0;
            }

            .radio-inline {
                margin-right: 15px;

                input {
                    margin-right: 4px;
                }
            }
        }

        &.year {
            .wrap-input {
                display: flex;
                justify-content: space-between;
            }

            .item {
                width: 130px;
            }
        }

        &.control_btn {
            .wrap-input {
                display: flex;
                justify-content: space-between;
            }

            .btn {
                border-radius: 10px;
                padding: 0px 40px;
            }
        }
    }

    .control_btn {
        display: flex;

        .btn {
            height: 42px;
            line-height: 42px;
        }

        .cancel {
            width: 30%;
        }

        .confirm {
            width: 67%;
            margin-left: 3%;
            color: #fff;
        }
    }

    .control_btn2 {
        display: flex;
        justify-content: center;

        .btn {
            margin: 0 15px;
        }
    }

    .confir_res {
        font-weight: 300;
        color: #666
    }
}


/*login-popup*/
.login-popup {
    padding: 0;
    .content_poup {
        padding: 0;
        border-radius: 0;
    }

    .form {
        width: 100%;
        padding: 15px;

        .tab {
            text-align: center;
            justify-content: center;
            margin: -15px -15px 34px -15px;
            background: #236852 url(images/graphics/bg-title-popup.png) no-repeat right;

            .login {
                text-align: right;
                position: relative;

                &::before {
                    content: "";
                    background: #eaeaea;
                    width: 1px;
                    bottom: -1px;
                    position: absolute;
                    right: -30px;
                    top: 0;
                }
            }

            .resign {
                text-align: left;
            }

            a {
                font-size: 18px;
                font-weight: 500;
                color: #fff;
                display: inline-block;
                padding: 28px 10px 10px 10px;
                position: relative;
                margin: 0 30px;

                &.active {
                    color: var(--yellow);
                    border-bottom: 2px solid var(--yellow);
                }
            }
        }

        .icon-visible {
            .form-control {
                padding-left: 34px;
            }

            svg {
                fill: var(--green1);
                position: absolute;
                top: 11px;
                left: 10px
            }
        }
        .request-pass{
            font-weight: 300;
        }
        .btn {
            width: 100%;
            @extend .radius10;
            margin-top: 20px;
            height: 42px;
        }

        .form-group {
            &__button {
                .btn {
                    width: auto;
                    border-radius: 0 10px 10px 0;
                    margin: 0;
                    height: 40px;
                }

            }
        }

        .login-social {
            margin-top: 25px;

            .center {
                position: relative;
                font-weight: 300;
                color: #666;
                font-size: 14px;
                margin-bottom: 25px;

                &:before {
                    content: "";
                    background: #eee;
                    height: 1px;
                    width: 100%;
                    position: absolute;
                    top: 10px;
                    left: 0;
                }

                span {
                    position: relative;
                    background: #fff;
                    padding: 0 10px;
                }
            }

            .flex {
                margin: 0 -15px;

                a {
                    width: 50%;
                    display: inline-block;
                    padding: 0 15px;

                    img {
                        width: 132px
                    }
                }
            }
        }
    }
    button.mfp-close{
        color:#fff;
        left: 0px;
    }
}

/*End login-popup*/